import React, { useState, useEffect } from "react";
import Unity, { UnityContext } from "react-unity-webgl";
import jwtDecode from "jwt-decode";

const unityContext = new UnityContext({
    loaderUrl: "build/Build/build.loader.js",
    dataUrl: "build/Build/build.data",
    frameworkUrl: "build/Build/build.framework.js",
    codeUrl: "build/Build/build.wasm"
});

function Home() {
    const [isLoaded, setIsLoaded] = useState(false);
    const [progression, setProgression] = useState(0);

    useEffect(function () {
        unityContext.on("loaded", function () {
            setIsLoaded(true);
        });
    }, []);

    useEffect(function () {
        unityContext.on("progress", function (progression) {
            setProgression(progression);
        });
    }, []);
    useEffect(function () {
        unityContext.on("GameController", function (message) {
            if (message == "Ready") {
                try {
                    window.onmessage = (e) => {
                        if (e.data.name === "iframe_message") {
                            if (e.data.token == "demo") {
                                unityContext.send("MenuManager", "RequestToken", JSON.stringify({
                                    userName: "demo",
                                    token: e.data.token,
                                    amount: e.data.allowanceAmount
                                }));
                            } else {
                                let user = jwtDecode(e.data.token);
                                unityContext.send("MenuManager", "RequestToken", JSON.stringify({
                                    userName: user.name,
                                    token: e.data.token,
                                    amount: e.data.allowanceAmount
                                }));
                            }
                        }
                    }
                    window.parent.postMessage({ name: "iframe_message" }, "*");
                } catch (err) {
                    console.log("error", err);
                }
            }
            else console.log("error");
        });
    }, []);

    return (
        <div className="main">
            {isLoaded ? (
                ""
            ) : <>
                <div className="loading">
                    <p className="percent" style={{ width: `${progression * 100}%` }}>{(progression * 100).toFixed("f0")}%</p>
                </div>
            </>}
            <Unity
                style={{ visibility: isLoaded ? "visible" : "hidden", height: "99.5vh" }}
                unityContext={unityContext}
                matchWebGLToCanvasSize={true}
            />
        </div>
    );
}

export default Home